@import url(https://fonts.googleapis.com/css2?family=MuseoModerno&family=Varela+Round&display=swap);
:root {
    --primary: #07020D;
    --secondary: #E6EBE0;
    --primary-color: #5CA4A9;
}

body, .uk-h1, .uk-h2, .uk-h3, .uk-h4, .uk-h5, .uk-h6, .uk-heading-2xlarge, .uk-heading-large, .uk-heading-medium, .uk-heading-small, .uk-heading-xlarge, h1, h2, h3, h4, h5, h6, .uk-navbar-item, .uk-navbar-nav>li>a, .uk-navbar-toggle {
    font-family: 'Varela Round', sans-serif;
}

.uk-logo {
    font-family: 'MuseoModerno', cursive;
}

.uk-heading-line>::after, .uk-heading-line>::before {
    border-bottom: calc(.2px + .05em) solid #333;
}

.uk-nav li>a {
    display: inline-block;
    color: white;

    margin-left: 5px;
    padding-left: 5px;
}

.uk-nav-default>li:nth-child(odd).uk-active>a {
    color: #0BAFC1;
    border-left: 1px dotted #0BAFC1;
    margin-left: 5px;
    padding-left: 5px;
}

.uk-nav-default>li:nth-child(even).uk-active>a {
    color: #E8E8C4;
    border-left: 1px dotted #E8E8C4;
    margin-left: 5px;
    padding-left: 5px;
}


.uk-section-primary, .uk-background-primary {
    background: #E6EBE0;
    background: var(--secondary);
}


.uk-section-secondary, .uk-background-secondary, .uk-navbar-container:not(.uk-navbar-transparent) {
    background: #07020D;
    background: var(--primary);
}

.shadow {
    text-shadow: 2px 2px black;

    color: #E6EBE0;

    color: var(--secondary);
    /* color: #333; */
    
}


.light {
    text-shadow: 2px 2px black;

    color: white;
    /* color: #333; */
    
}

.highlight {

    background-size: 200% 100%;
    background-image: linear-gradient(to right,transparent 50%, #5CA4A9 50%);
    background-image: linear-gradient(to right,transparent 50%, var(--primary-color) 50%);
    /* color: white; */
    background-origin: 0;
    background-size: 200% 50%;
    background-repeat: repeat-x;
    background-position: 0 100%;
    transition: background-position 0.5s;
}
.highlight-alt {

    background-size: 200% 100%;
    background-image: linear-gradient(to right,transparent 50%, #E6EBE0  50%);
    background-image: linear-gradient(to right,transparent 50%, var(--secondary)  50%);
    /* color: white; */
    background-origin: 0;
    background-size: 200% 50%;
    background-repeat: repeat-x;
    background-position: 0 100%;
    transition: background-position 0.5s;
}

.highlight:hover, .highlight-alt:hover {
    background-position: -100% 100%;
}

.uk-nav-default>li.uk-active>a>span:hover, .uk-nav-default>li>a>span:focus, .uk-nav-default>li>a>span:hover {
    background-color: #5CA4A9;
    background-color: var(--primary-color);
    color: #f5f5f5;
}

.uk-nav-default>li>a:focus, .uk-nav-default>li>a:hover {
    color: #f5f5f5;

}

.uk-card-primary.uk-card-body .uk-label, .uk-card-primary>:not([class*=uk-card-media]) .uk-label, .uk-card-secondary.uk-card-body .uk-label, .uk-card-secondary>:not([class*=uk-card-media]) .uk-label, .uk-light .uk-label, .uk-offcanvas-bar .uk-label, .uk-overlay-primary .uk-label, .uk-section-primary:not(.uk-preserve-color) .uk-label, .uk-section-secondary:not(.uk-preserve-color) .uk-label, .uk-tile-primary:not(.uk-preserve-color) .uk-label, .uk-tile-secondary:not(.uk-preserve-color) .uk-label {
    background-color: #5CA4A9;
    background-color: var(--primary-color);
    color: #f5f5f5;
}
